
.index {
  padding-bottom: 2rem;
}
.top {
  text-align: center;
  padding-top: 4rem;
  height: 100vh;
  padding-bottom: 4rem;
  position: relative;
}
.top h1 {
    font-size: 0.8rem;
}
.top .job {
    font-size: 0.65rem;
    margin-top: 0.2rem;
}
.top .info {
    margin-top: 2rem;
    color: #1f4242;
    font-size: 0.5rem;
    line-height: 2;
}
section {
  margin-bottom: 0.4rem;
  color: #2f8086;
}
section .sub-title {
    background: #77d9cc;
    color: #fff;
    padding: 0.4rem 0.4rem;
    font-size: 0.48rem;
}
section .con {
    padding: 0.6rem 0.4rem;
}
section .company-name {
    font-size: 0.44rem;
}
section .time {
    color: silver;
    display: block;
    margin: 0.2rem 0;
    font-size: 0.36rem;
}
section .work {
    color: #333;
    font-size: 0.36rem;
}
section .work p {
      line-height: 2;
}
.project {
  font-size: 0.44rem;
}
.project p:nth-of-type(2) {
    color: #333;
    margin-top: 0.4rem;
    font-size: 0.36rem;
    line-height: 2;
}
.project a {
    display: block;
    text-align: center;
    width: 2rem;
    padding: 0.2rem 0;
    color: #8fb6b5;
    border: 1px solid #8fb6b5;
    margin-top: 0.4rem;
    font-size: 0.36rem;
    border-radius: 4px;
}
.project li {
    padding-bottom: 0.6rem;
    border-bottom: 1px solid silver;
}
.project li:not(:first-of-type) {
      margin-top: 0.6rem;
}
.icon-github-wrap {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.icon-github-wrap i {
    font-size: 0.8rem;
    color: #333;
}
.icon {
  width: 1rem;
  height: 1rem;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  font-size: 30px;
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  min-width: 2rem;
}
.skills {
  font-size: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0.6rem 0;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align: center;
}
.skills p {
    margin-bottom: 0.6rem;
}
.skills span {
    font-size: 0.4rem;
    display: block;
    margin-top: 0.2rem;
    color: #333;
}
.my-info {
  color: #333;
  font-size: 0.4rem;
  line-height: 1.8;
}



