
.luck_draw {
  position: fixed;
  left: 50%;
  top: 50%;
  max-width: 600px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 22;
  background: #f4efeb;
}
.luck_draw ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-left: -0.2rem;
    margin-top: 2rem;
    padding: 0 0.4rem;
}
.luck_draw li {
    -webkit-box-flex: 30%;
        -ms-flex: 30%;
            flex: 30%;
    text-align: center;
    background: salmon;
    height: 2rem;
    line-height: 2rem;
    margin-left: 0.2rem;
    margin-top: 0.2rem;
    position: relative;
}
.luck_draw li.hide span {
      display: none;
}
.luck_draw li span {
      background: black;
      pointer-events: none;
      opacity: .5;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
}
.luck_draw li:nth-of-type(1) {
      background: #6bdfef;
      color: #238997;
}
.luck_draw li:nth-of-type(2) {
      background: #94eb6b;
      color: #179529;
}
.luck_draw li:nth-of-type(3) {
      background: #efcb6b;
      color: #826311;
}
.luck_draw li:nth-of-type(4) {
      background: #ef696b;
      color: #a50406;
}
.luck_draw li:nth-of-type(5) {
      background: #f73232;
      color: white;
      cursor: pointer;
}
.luck_draw li:nth-of-type(5) span {
        display: none;
}
.luck_draw li:nth-of-type(6) {
      background: #ef69b5;
      color: #a2065f;
}
.luck_draw li:nth-of-type(7) {
      background: #ad69ef;
      color: #680c73;
}
.luck_draw li:nth-of-type(8) {
      background: #54e068;
      color: #047814;
}
.luck_draw li:nth-of-type(9) {
      background: #ef696b;
      color: #a50406;
}



