
@import url(./../static/icon/iconfont.css);
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
a {
  text-decoration: none;
}
ul, li {
  list-style: none;
}
html, body {
  width: 100%;
  overflow-x: hidden;
}
html {
  background: #f4efeb;
  color: #2f8086;
}
@media screen and (min-width: 600px) {
html {
    font-size: 40px !important;
    background: #333;
}
body {
    max-width: 600px;
    margin: 0 auto;
    background: #f4efeb;
    color: #2f8086;
}
}



