
.upload-wrap {
  background: #fff;
  width: 90%;
  margin: 0.6rem auto;
  padding: 0.6rem;
  position: relative;
  overflow: hidden;
}
.upload-wrap input[type=file] {
    position: absolute;
    opacity: 0;
    display: none;
}
.upload-input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.upload-input label, .upload-input .images-box {
    margin-right: 0.6rem;
    margin-bottom: 0.6rem;
    min-width: 2rem;
    width: 2rem;
    height: 2.5rem;
    border: 1px solid #efefef;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.upload-input label img, .upload-input .images-box img {
      max-width: 100%;
      -o-object-fit: contain;
         object-fit: contain;
}
.upload-input label > div, .upload-input .images-box > div {
      overflow: hidden;
      height: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.upload-input label .icon-guanbi, .upload-input .images-box .icon-guanbi {
      position: absolute;
      left: 100%;
      bottom: 100%;
      -webkit-transform: translate(-50%, 50%);
              transform: translate(-50%, 50%);
      background: #fff;
      border-radius: 100%;
      overflow: hidden;
}
.upload-input label i.icon-icon1460189731522, .upload-input .images-box i.icon-icon1460189731522 {
      pointer-events: none;
      color: slategrey;
}
.loading-animate {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 45;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.loading-animate div {
    background: rgba(0, 0, 0, 0.7);
    padding: 0.4rem;
    border-radius: 4px;
}
.loading-animate div span {
      display: block;
      width: 1rem;
      height: 1rem;
      border-radius: 100%;
      border: 2px solid #fff;
      border-top-color: transparent;
      -webkit-animation: loading 1s infinite linear;
              animation: loading 1s infinite linear;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



